import styled from 'styled-components';

// configs
import colors from '../../configs/colors';

const ContentTextWrap = styled.div`
  width: 80%;
  margin: 0 auto;
  padding: 1rem 0;
  color: ${colors.purple};
  font-size: 1.5rem;
  font-weight: bold;
`

export default ContentTextWrap;


